// import axios from 'axios';
import { httpService } from './http.service'
import Axios from 'axios';
var axios = Axios.create({ withCredentials: true, });

export const toyService = {
  query,
  remove,
  save,
  getEmptyToy,
  getById,
  getLabels,
};

window.cs = toyService;



async function query(filterBy = {}) {
  try {
    const toys = await httpService.get('toy/', { params: filterBy });
    return toys;
  } catch (err) {
    console.log('err with query toys', err);
  }
}

async function getLabels() {
  try {
    const labels = await httpService.get('toy/label');
    return labels;
  } catch (err) {
    console.log('err while getting labels', err);
  }
}

async function getById(id) {
  try {
    const toy = await httpService.get(`toy/${id}`);
    return toy;
  } catch (err) {
    console.log('err while getting toy id:' + id, err);
  }
}

async function remove(toyId) {
  try {
    await httpService.delete(`toy/${toyId}`);
    return toyId;
  } catch (err) {
    console.log('err while removing toy with id:' + toyId, err);
  }
}

async function save(toy) {
  if (toy._id) {
    try {
      const savedToy = await httpService.put(`toy/${toy._id}`, toy);
      // console.log(savedToy);
      return savedToy;
    } catch (err) {
      console.log('err while updating toy:' + toy, err);
    }
  } else {
    try {
      const savedToy = await httpService.post('toy/', toy);
      return savedToy.ops[0];
    } catch (err) {
      console.log('err while saving toy:' + toy, err);
    }
  }
}

function getEmptyToy() {
  return {
    // _id: '',
    name: '',
    price: null,
    labels: [],
    createdAt: null,
    inStock: true,
  };
}
