<template>
  <div class="container" v-if="toy">
    <h2>Lets Chat About {{toy.name}}</h2>
    <ul>
      <li v-for="(msg, idx) in msgs" :key="idx">
        <span>{{msg.from}}:</span>
        {{msg.txt}}
      </li>
    </ul>
         <small v-if="isTyping">{{ typingUser }} is typing...</small>
    <hr />
    <form @submit.prevent="sendMsg">
      <input @input="typing" type="text" v-model="msg.txt" placeholder="Your msg" />
      <button>Send</button>
    </form>
  </div>
</template>

<script>
import { socketService } from "@/services/socket.service";

export default {
  props: {
    toy: {
      type: Object,
    //   required: 
    }
  },
  data() {
    return {
      msg: { from: '', txt: "" },
      msgs: [],
      topic: '',
      isTyping: false,
      typingUser: null,
    };
  },
  created() {
    // socketService.setup();
    this.topic = this.toy._id
    socketService.emit("chat topic", this.topic);
    socketService.on("chat userTyping",this.userTyping);
    socketService.on("chat addMsg", this.addMsg);
    this.msg.from = this.user.fullname
  },
  computed:{
user(){
    return this.$store.getters.user
}
  },
  destroyed() {
    socketService.off("chat addMsg", this.addMsg);
    // socketService.terminate();
  },
  methods: {
    addMsg(msg) {
      this.msgs.push(msg);
    },
    sendMsg() {
        this.isTyping = false;
      this.typingUser =  '';
      console.log("Sending", this.msg);
      socketService.emit("chat newMsg", this.msg);
      // TODO: next line not needed after connecting to backend
      // this.addMsg(this.msg)
      // setTimeout(()=>this.addMsg({from: 'Dummy', txt: 'Yey'}), 2000)
      this.msg = { from: this.user.fullname, txt: "" };
    },
    userTyping(){

        this.isTyping = true;
      this.typingUser =  this.user.fullName;
    },
    typing(){
      socketService.emit("chat sendTyping", this.user.fullname);
    },
    changeTopic() {
      socketService.emit("chat topic", this.topic);
    }
  }
};
</script>
